<h1 style="text-align: center;">Śr&oacute;droczna punktacja zastęp&oacute;w 15 MDH</h1>
<h2>Wstęp</h2>
<p>Punktacja opiera się na 4 <em>elementach</em>. Każdy element ma określoną maksymalną liczbę punkt&oacute;w do zdobycia:</p>
<ol>
<li><strong>Zbi&oacute;rka zastępu</strong>: 10 punkt&oacute;w</li>
<li><strong>Zbi&oacute;rka drużyny</strong>: 25 punkt&oacute;w</li>
<li><strong>Biwak: </strong>50 punkt&oacute;w</li>
<li><strong>Sprawności</strong></li>
</ol>
<p>Każdy <em>element </em>jest oceniany przez poszczeg&oacute;lne <em>kryteria </em>elementu. Maksymalna pula punkt&oacute;w może być zdobyta poprzez wypełnienie wszystkich <em>kryteri&oacute;w</em>. <strong>Sprawności </strong>liczone są osobno.&nbsp;</p>
<p>Na stronie <a href="https://zuberek.github.io/15mdh/" target="_blank" rel="noopener">https://zuberek.github.io/15mdh/</a> można zobaczyć aktualne wyniki turnieju.</p>
<h2>Szczeg&oacute;ły</h2>
<h3>1. Zbi&oacute;rka zastępu</h3>
<p>Za każdą zbi&oacute;rkę można zdobyć 10 punkt&oacute;w. Punkty podzielone są na kryteria:</p>
<ul>
<li><strong>Frekwencja</strong>: 4 punkty</li>
<li><strong>Regularność</strong>: 3 punkty</li>
<li><strong>Przygotowanie</strong>: 3 punkty</li>
</ul>
<h4>Frekwencja</h4>
<p>To jaka część zastępu będzie na zbi&oacute;rce.</p>
<div>
<table>
<tbody>
<tr>
<td>1 punkt</td>
<td>2 punkty</td>
<td>3 punkty</td>
<td>4 punkty</td>
</tr>
<tr>
<td>co najmniej 10%</td>
<td>co najmniej 50%</td>
<td>co najmniej 60%</td>
<td>co najmniej 80%</td>
</tr>
</tbody>
</table>
</div>
<h4>Regularność</h4>
<p>Ile zbi&oacute;rek z rzędu udało się zorganizować. Liczy się to jak nieprzerwany jest ciąg.</p>
<div>
<table>
<tbody>
<tr>
<td>1 punkt</td>
<td>2 punkty</td>
<td>3 punkty</td>
</tr>
