<td>pierwsza zbi&oacute;rka</td>
<td>zbi&oacute;rka jest druga w ciągu</td>
<td>zbi&oacute;rka jest trzecia i dalej</td>
</tr>
</tbody>
</table>
</div>
<h4>Przygotowanie</h4>
<p>Jak wcześniej zbi&oacute;rka będzie przygotowana i harcerze poinformowani.</p>
<div>
<table>
<tbody>
<tr>
<td>1 punkt</td>
<td>2 punkty</td>
<td>3 punkty</td>
</tr>
<tr>
<td>dzień przed (sobota)</td>
<td>trzy dni przed (środa)</td>
<td>tydzień przed (na zbi&oacute;rce)</td>
</tr>
</tbody>
</table>
</div>
<h3>2. Zbi&oacute;rka drużyny</h3>
<p>Na każdej zbi&oacute;rce drużyny zastęp może zdobyć do 25 punkt&oacute;w, podzielone na kryteria:</p>
<ul>
<li><strong>Umundurowanie</strong>: 5 punkt&oacute;w</li>
<li><strong>Punktualność</strong>: 3 punkty</li>
<li><strong>Wyniki</strong>: 15 punkt&oacute;w</li>
<li><strong>Inne</strong>: 2 punkty</li>
</ul>
<h4>Umundurowanie</h4>
<p>Liczy się dla każdej osoby w zastępie, bez wyjątk&oacute;w. Stworzyliśmy podział pomiędzy <em>mundurem, </em>a <em>elementami umundurowania</em>:</p>
<p><em>Mundur</em>:</p>
<ul>
<li>Koszula mundurowa</li>
<li>Spodnie mundurowe</li>
</ul>
<p><em>Elementy umundurowania</em>:</p>
<ul>
<li>Buty mundurowe</li>
<li>Pasek</li>
<li>Rogatywka</li>
<li>Patka drużyny</li>
<li>Getry</li>
</ul>
<p>Do 3 punkt&oacute;w można zdobyć jeżeli każdy harcerz przyjdzie w <em>mundurze</em>. Czyli nawet braki w <em>elementach umundurowania</em> wtedy się nie liczą. Żeby zdobyć więcej niż 3 punkty, wszyscy są w <em>mundurze </em>plus liczy się ilość błęd&oacute;w, to jest brak&oacute;w w <em>elementach umundurowania.</em></p>
<div>
<table>
<tbody>
<tr>
<td>1 punkt</td>
<td>2 punkty</td>
<td>3 punkty</td>
<td>4 punkty</td>
<td>5 punkt&oacute;w</td>
</tr>
<tr>
<td>50% zastępu w mundurze</td>
<td>70% zastępu w mundurze</td>
<td>100% zastępu w mundurze</td>
<td>4 błędy na cały zastęp</td>
<td>2 błędy na cały zastęp</td>
</tr>
</tbody>
</table>
</div>
<h4>Punktualność</h4>
<p>To kiedy przyszła ostatnia osoba z zastępu po rozpoczęciu zbi&oacute;rki.</p>
<div>
<table>
<tbody>
<tr>
<td>1 punkt</td>
