<td>3 punkty</td>
</tr>
<tr>
<td>15 minut po rozpoczęciu</td>
<td>5 minut po rozpoczęciu</td>
<td>wszyscy byli na czas</td>
</tr>
</tbody>
</table>
</div>
<h4>Wyniki</h4>
<p>Wyniki gry na zbi&oacute;rce. Każda gra będzie miała własną, szczeg&oacute;łową punktację dającą <em>wynik</em>. Zastęp z najlepszym <em>wynikiem </em>zdobywa 15 punkt&oacute;w. Kolejne zastępy zdobywają ilość punkt&oacute;w proporcjonalną do tego, jak bliski był ich <em>wynik </em>do <em>wyniku </em>najlepszego zastępu.&nbsp;</p>
<p>Czyli na przykład: najlepszy zastęp zdobył 150 punkt&oacute;w w szczeg&oacute;łowej punktacji gry i dostaje 15 punkt&oacute;w. Drugi najlepszy zastęp zdobył 140 punkt&oacute;w. 140 to duża część 150, dlatego ten zastęp dostaje 14 punkt&oacute;w: 140/150 x 15 = 14.</p>
<h4>Inne</h4>
<p>Prowadzący zbi&oacute;rkę mogą wynagrodzić zastępy odznaczające się wyjątkowym zaangażowaniem, dodając im do dw&oacute;ch dodatkowych punkt&oacute;w.</p>
<h3>3. Biwak</h3>
<p>Za udział w biwaku zastęp może zdobyć do 50 punkt&oacute;w, podzielonych na:</p>
<ul>
<li><strong>umundurowanie</strong>: 8 punkt&oacute;w</li>
<li><strong>punktualność na zbi&oacute;rce wyjazdowej</strong>: 3 punkty</li>
<li><strong>punktualność na biwaku</strong>: 6 punkt&oacute;w</li>
<li><strong>porządki</strong>: 8 punkt&oacute;w</li>
<li><strong>wyniki</strong>: 25 punkt&oacute;w</li>
</ul>
<h4>Umundurowanie</h4>
<p>Jest oceniane na zbi&oacute;rce wyjazdowej. Podobnie jak dla Zbi&oacute;rki Drużyny, dalej obowiązuje podział na <em>mundur </em>i <em>elementy umundurowania</em>, ale rozłożenie punkt&oacute;w i wymagania są inne.</p>
<div>
<table>
<tbody>
