<td>2 punkty</td>
<td>4 punkty</td>
<td>6 punkt&oacute;w</td>
<td>8 punkt&oacute;w</td>
</tr>
<tr>
<td>70% zastępu w mundurze</td>
<td>100% zastępu w mundurze</td>
<td>4 błędy na cały zastęp</td>
<td>2 błędy na cały zastęp</td>
</tr>
</tbody>
</table>
</div>
<h4>Punktualność na zbi&oacute;rce wyjazdowej</h4>
<p>Liczone identycznie jak Punktualność na Zbi&oacute;rce Drużyny czyli to kiedy przyszła ostatnia osoba z zastępu po rozpoczęciu zbi&oacute;rki.</p>
<div>
<table>
