<td>3 punkty</td>
</tr>
<tr>
<td>15 minut po rozpoczęciu</td>
<td>5 minut po rozpoczęciu</td>
<td>wszyscy byli na czas</td>
</tr>
</tbody>
</table>
</div>
<h4>Punktualność na biwaku</h4>
<p>Zastępy rywalizują kt&oacute;ry pierwszy będzie got&oacute;w po ogłoszeniu zbi&oacute;rki, otrzymując 4 punkty za pierwsze miejsce, 3 za drugie, 2 za trzecie, 1 za czwarte. Pojedyncze wyniki naliczają się podczas biwaku i zależnie od ostatecznego miejsca otrzymuje się punkty.</p>
<div>
<table>
<tbody>
<tr>
<td>1 punkt</td>
<td>2 punkty</td>
<td>4 punkty</td>
<td>6 punkt&oacute;w</td>
</tr>
<tr>
<td>4 miejsce</td>
<td>3 miejsce</td>
<td>2 miejsce</td>
<td>1 miejsce</td>
</tr>
</tbody>
</table>
</div>
<h4>Porządki</h4>
<p>Sprawdzanie porządku w namiocie. Każdy błąd, czyli rzeczy nie znajdujące się tam gdzie powinny, powoduje utratę punkt&oacute;w. Zastęp może podjąć decyzję czy chce pozwolić na <em>dokładne </em>sprawdzenie porządku czy <em>pobieżne</em>. Za <em>dokładne </em>sprawdzenie (wszystko poza tym co jest w plecaku) można zdobyć do 8 punkt&oacute;w ale każdy błąd powoduje większą utratę punkt&oacute;w. <em>Pobieżne </em>sprawdzenie daje maksymalnie 4 punkty.</p>
