<td>2 błędy</td>
</tr>
</tbody>
</table>
</div>
<h4>Wyniki</h4>
<p>W kryterium wyniki na biwaku, na maksymalne 25 punkt&oacute;w składają się wszystkie gry przeprowadzone na biwaku. Szczeg&oacute;łowe <em>wyniki </em>gier są sumowane i zastęp z najwyższą sumą zdobywa pełne 25 punkt&oacute;w w turnieju. Dalej, podobnie jak w Wynikach dla Zbi&oacute;rki Drużyny, każdy zastęp otrzymuje proporcjonalną ilość punkt&oacute;w w stosunku do wyniku najlepszego zastępu.</p>
<h3>4. Sprawności</h3>
<p>Każde przyznanie sprawności harcerzowi dodaje punkt&oacute;w zastępowi w turnieju. Trudniejsze sprawności nagradzają większą ilością punkt&oacute;w. Sprawności przyznaje się na zbi&oacute;rce drużyny oraz biwaku.</p>
